.dropdown {
 position: relative;
 display: flex;
 justify-content: center;
 margin: 2rem auto;
}

.menu-button{
 background-color: orange;
 padding: .5rem 4rem;
 font-size: 2rem;
}

.menu-button:hover{
 cursor: pointer;
 background-color: darkorange;
}
.dropdown-content {
 display: none;
 position: absolute;
 background-color: orange;
 box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
 z-index: 1;
}

.dropdown-content a {
 color: black;
 padding: 12px 16px;
 text-decoration: none;
 display: block;
}

.dropdown-content a:hover {
 background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
 display: block;
}